import { Overlay } from '@angular/cdk/overlay';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import {BehaviorSubject, Observable, Subject} from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { Employee, EmployeeService } from 'src/app/core';
import { CoreService } from 'src/app/core/services/core.service';
import { EmployeeEditorDialogComponent } from '../../components/employee-editor-dialog/employee-editor-dialog.component';
import {MockEmployee} from '../../../core/mock/mockEmployee';
import {MockEmployeeList} from '../../../core/mock/mockEmployeeList';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'crm-employee-detail',
  templateUrl: './employee-detail.component.html',
  styleUrls: ['./employee-detail.component.scss']
})
export class EmployeeDetailComponent implements OnInit {
  displayModal: boolean;

  employee$: Observable<Employee>;
  loading$: Subject<boolean> = new BehaviorSubject<boolean>(true);

  employees$: Observable<Employee[]>;


  constructor(private route: ActivatedRoute,
              private router: Router, private employeeService: EmployeeService, private coreService: CoreService,
              private dialog: MatDialog, private overlay: Overlay) {
    this.displayModal = false;
    this.loading$.next(true);
    this.employees$ = this.employeeService.getEmployees();

    this.employee$ = this.route.paramMap.pipe(
       switchMap(params => this.employeeService.getEmployee(params.get('id')!)),
      );
    this.employee$.subscribe(() => this.loading$.next(false));
  }

  ngOnInit(): void {

  }

  getLoading(): Observable<boolean> {
    return this.loading$;
  }

  // tslint:disable-next-line:typedef
  handleUpdate(empl: Employee) {
    const dialogRef = this.dialog.open(EmployeeEditorDialogComponent, {
      width: '800px',
      height: 'auto',
      disableClose: true,
      autoFocus: true,
      scrollStrategy: this.overlay.scrollStrategies.noop(),
      data: empl
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result) {

      }
    });
  }

    showModalDialog() {
        this.displayModal = true;
    }

  showEmpInformation(id: string) {

  }

  updateProfile() {
    // @ts-ignore
    const fullname: string = document.getElementById("fullname").value;
    const emp: Employee = {
      id: '7',
      fullName: fullname
    }
    alert(JSON.stringify(emp, null, 2));
    //this.employee$ = this.route.paramMap.pipe(
    //    switchMap(params => this.employeeService.updateEmployee(params.get('id')!, emp)),
    //);
  }
}
